export const CHANGE_APPLICATION_LANGUAGE =
  "APPLICATION/CHANGE_APPLICATION_LANGUAGE";

export const applicationModule = {
  state: {
    token: undefined,
    host: "https://",
    language: "ru",
  },
  actions: {},
  getters: {
    getApplicationLanguage: (state) => state.language,
  },
  mutations: {
    [CHANGE_APPLICATION_LANGUAGE]: (state, language) => {
      state.language = language;
    },
  },
};
