<template>
  <div class="text-input" :class="rootClass">
    <TypographyWithInfo
      v-if="!!label"
      font-weight="medium"
      :class="labelClasses"
      :info-text="infoText"
      class="text-input__label"
      :popover-color="popoverColor"
      :info-popover-width="infoPopoverWidth"
    >
      {{ label }}
    </TypographyWithInfo>

    <div
      @click="onRootClick"
      :class="containerClasses"
      @mouseenter="hover = true"
      @mouseleave="hover = false"
      class="text-input__container"
    >
      <slot name="startAdornment" v-if="hasStartAdornment" />

      <input
        ref="input"
        v-bind="$attrs"
        :value="modelValue"
        @blur="focus = false"
        @focus="focus = true"
        @click="onFieldClick"
        :class="fieldClasses"
        class="text-input__field"
        @input="$emit('update:modelValue', $event.target.value)"
      />

      <div class="text-input__end-adornment-container">
        <slot name="endAdornment" v-if="hasEndAdornment" />
      </div>

      <TextCounter
        v-if="showCounter"
        :text="modelValue"
        :class="counterClasses"
        class="text-input__counter"
        :max-count="$attrs.maxlength"
      />
    </div>

    <VTypography font-style="text-2" v-if="!!error" class="text-input__error">{{
      error
    }}</VTypography>
  </div>
</template>

<script>
import TextCounter from "@/components/ui/TextCounter";
import VTypography from "@/components/ui/Typography";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";

export default {
  name: "TextInput",
  inheritAttrs: false,
  components: {
    VTypography,
    TextCounter,
    TypographyWithInfo,
  },
  props: {
    label: String,
    error: String,
    infoText: String,
    rootClass: String,
    modelValue: String,
    popoverColor: String,
    infoPopoverWidth: Number,
    inputType: {
      type: String,
      default: "default",
      validator(value) {
        return ["default", "contrast"].includes(value);
      },
    },
  },
  data() {
    return {
      hover: false,
      focus: false,
    };
  },
  computed: {
    hasEndAdornment() {
      return !!this.$slots.endAdornment;
    },
    hasStartAdornment() {
      return !!this.$slots.startAdornment;
    },
    containerClasses() {
      return {
        "text-input__container--focus": this.focus,
        "text-input__container--error": !!this.error,
        "text-input__container--hover": !this.focus && this.hover,
        "text-input__container--contrast": this.inputType === "contrast",
      };
    },
    fieldClasses() {
      return {
        "text-input__field--contrast": this.inputType === "contrast",
      };
    },
    showCounter() {
      return !!this.$attrs.maxlength && (!!this.modelValue || this.focus);
    },
    labelClasses() {
      return {
        "text-input__label--contrast": this.inputType === "contrast",
      };
    },
    counterClasses() {
      return {
        "text-input__counter--contract": this.inputType === "contrast",
      };
    },
  },
  methods: {
    onRootClick() {
      this.$refs.input.focus();

      const value = this.$refs.input.value;

      this.$refs.input.value = "";
      this.$refs.input.value = value;
    },
    onFieldClick(event) {
      event.stopPropagation();
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/mixins.scss";
@import "~@/assets/styles/variables.scss";

.text-input {
  display: flex;
  flex-direction: column;
}

.text-input__info-popover {
  margin-left: 10px;
}

.text-input__container {
  @include transition(border-color);
  height: 45px;
  display: flex;
  border: 2px solid $gray4;
}

.text-input__field {
  padding: 0;
  flex-grow: 1;
  border: none;
  outline: none;
  margin: 0 13px;
  flex-shrink: 1;
  background-color: transparent;
  font-weight: $font-weight-medium;
}

.text-input__field--contrast {
  color: $white;
}

.text-input__label {
  margin-bottom: 10px;
}

.text-input__label--contrast {
  color: $white;
}

.text-input__container--hover {
  border-color: $gray1;
}

.text-input__container--contrast {
  border-color: $white;
}

.text-input__container--focus {
  border-color: $green;
}

.text-input__container--error {
  border-color: $red;
  margin-bottom: 5px;
}

.text-input__counter {
  align-self: center;
  margin-right: 13px;
}

.text-input__counter--contract {
  color: $gray2;
}

.text-input__end-adornment-container {
  display: flex;
  margin-right: 13px;
  align-items: center;
  justify-content: center;
}

.text-input__error {
  color: $red;
}
</style>
