import { FormsStatus } from "@/constants/FormConstants";
import { createDraftFormsItem } from "@/helpers/FormsHelpers";
import { deepUpdate } from "immupdate";

export const FormsMutations = Object.freeze({
  ChangeFormsType: "Forms/ChangeFormsType",
  CreateDraftForm: "Forms/CreateDraftForm",
  GetFormsItemsFulfill: "Forms/GetFormsItemsFulfill",
  GetFormsItemsPerform: "Forms/GetFormsItemsPerform",
  GetFormsItemsReject: "Forms/GetFormsItemsReject",
  GetFormsListFulfill: "Forms/GetFormsListFulfill",
  GetFormsListPerform: "Forms/GetFormsListPerform",
  GetFormsListReject: "Forms/GetFormsListReject",

  UpdateFormInfo: "Forms/UpdateFormInfo",

  UpdateGeneralFormMutation: "Forms/UpdateGeneralFormMutation",

  AddAuthorMutation: "Forms/AddAuthorMutation",
});

export const mutations = {
  [FormsMutations.AddAuthorMutation]: (state, { id, data }) => {
    const authors = state.items[id].forms.authors.instituteAuthors.slice();

    authors.push(data);

    state.items = deepUpdate(state.items)
      .at(id)
      .at("forms")
      .at("authors")
      .at("instituteAuthors")
      .set(authors);
  },
  [FormsMutations.UpdateFormInfo]: (state, { id, data }) => {
    state.items = {
      ...state.items,
      [id]: {
        ...state.items[id],
        ...data,
      },
    };
  },
  [FormsMutations.CreateDraftForm]: (state) => {
    state.items = {
      ...state.items,
      [FormsStatus.Draft]: createDraftFormsItem(),
    };
  },
  [FormsMutations.GetFormsItemsPerform]: (state, { meta }) => {
    state.items = {
      ...state.items,
      [meta.id]: {
        fetching: true,
      },
    };
  },
  [FormsMutations.GetFormsItemsFulfill]: (state, { meta, payload }) => {
    state.items = {
      ...state.items,
      [meta.id]: {
        fetching: false,
        payload,
      },
    };
  },
  [FormsMutations.GetFormsItemsReject]: (state, { meta, error }) => {
    state.items = {
      ...state.items,
      [meta.id]: {
        fetching: false,
        error,
      },
    };
  },
  [FormsMutations.ChangeFormsType]: (
    state,
    { id = FormsStatus.Draft, type }
  ) => {
    state.items = {
      ...state.items,
      [id]: {
        ...state.items[id],
        type,
      },
    };
  },
  [FormsMutations.UpdateGeneralFormMutation]: (
    state,
    { id = FormsStatus.Draft, form }
  ) => {
    state.items = {
      ...state.items,
      [id]: {
        ...state.items[id],
        general: form,
      },
    };
  },
};
