<template>
  <VLanguage v-model="language" :languages="['ru', 'en']" />
</template>

<script>
import VLanguage from "@/components/ui/Language";
import { CHANGE_APPLICATION_LANGUAGE } from "@/store/modules/applicationModule";

export default {
  name: "AppLanguage",
  components: { VLanguage },
  computed: {
    language: {
      set(language) {
        this.$store.commit(CHANGE_APPLICATION_LANGUAGE, language);
      },
      get() {
        return this.$store.getters.getApplicationLanguage;
      },
    },
  },
};
</script>
