import { FormsStatus } from "@/constants/FormConstants";

export const FormsGetters = Object.freeze({
  GetFormsList: "Forms/GetFormsList",
  GetFormsListError: "Forms/GetFormsListError",
  IsFormsListFetching: "Forms/IsFormsListFetching",
  GetFormsDraftItem: "Forms/GetFormsDraftItem",
  GetFormType: "Forms/GetFormType",
  GetFormName: "Forms/GetFormName",
  GetFormStatus: "Forms/GetFormStatus",
  GetFormsItem: "Forms/GetFormsItem",
  GetFormsItemError: "Forms/GetFormsItemError",
  IsFormsItemFetching: "Forms/IsFormsItemFetching",

  GetFormInfo: "Forms/GetFormInfo",

  GetGeneralForm: "Forms/GetGeneralForm",

  GetInstituteAuthors: "Forms/GetInstituteAuthors",
});

export const getters = {
  [FormsGetters.GetInstituteAuthors]: ({ items }) =>
    items.draft.forms.authors.instituteAuthors,
  [FormsGetters.GetFormsList]: ({ list }) => list.payload,
  [FormsGetters.GetFormsListError]: ({ list }) => list.error,
  [FormsGetters.IsFormsListFetching]: ({ list }) => list.fetching,
  [FormsGetters.GetFormsDraftItem]: ({ items }) => items[FormsStatus.Draft],
  [FormsGetters.GetFormInfo]:
    ({ items }) =>
    (id) => {
      const { type, name, status } = items[id] || {};

      return { type, name, status };
    },
  [FormsGetters.GetFormType]:
    ({ items }) =>
    (id = FormsStatus.Draft) =>
      items[id]?.type,
  [FormsGetters.GetFormName]:
    ({ items }) =>
    (id = FormsStatus.Draft) =>
      items[id]?.name,
  [FormsGetters.GetFormStatus]:
    ({ items }) =>
    (id = FormsStatus.Draft) =>
      items[id]?.status,
  [FormsGetters.GetFormsItem]:
    ({ items }) =>
    (id = FormsStatus.Draft) =>
      items[id],
  [FormsGetters.GetFormsItemError]:
    ({ items }) =>
    (id = FormsStatus.Draft) =>
      items[id]?.error,
  [FormsGetters.IsFormsItemFetching]:
    ({ items }) =>
    (id = FormsStatus.Draft) =>
      items[id]?.fetching || false,
  [FormsGetters.GetGeneralForm]:
    ({ items }) =>
    (id = FormsStatus.Draft) =>
      items[id]?.forms.general,
};
