<template>
  <div class="v-info" v-click-outside="onClickOutside">
    <VButton
      @click="onClick"
      @mouseenter="onMouseEnter"
      @mouseleave="onMouseLeave"
      class="v-info__icon-container"
    >
      <div ref="iconContainer">
        <slot :active="show" name="icon"></slot>
      </div>
    </VButton>

    <div
      ref="popover"
      :class="popoverClasses"
      class="v-info__popover"
      :style="{
        top: `${-top}px`,
        left: `${left}px`,
        width: `${width}px`,
        backgroundColor: color,
      }"
    >
      <VTypography class="v-info__popover-title" font-weight="medium">{{
        text
      }}</VTypography>
    </div>
  </div>
</template>

<script>
import VTypography from "@/components/ui/Typography";
import VButton from "@/components/buttons/VButton";

export default {
  name: "VPopover",
  components: { VButton, VTypography },
  emits: {
    click: Function,
  },
  props: {
    color: {
      type: String,
      default: "#161615",
    },
    text: {
      type: String,
      required: true,
    },
    triggerType: {
      type: String,
      default: "click",
      validator(value) {
        return ["click", "hover"].includes(value);
      },
    },
    width: {
      type: Number,
      required: true,
    },
    position: {
      type: String,
      default: "right",
      validator(value) {
        return ["left", "center", "right"].includes(value);
      },
    },
  },
  data() {
    return {
      top: 0,
      left: 20,
      show: false,
    };
  },
  computed: {
    popoverClasses() {
      return {
        "v-info__popover--show": this.show,
      };
    },
  },
  updated() {
    if (this.show) {
      const popoverHeight = this.$refs.popover.clientHeight;
      const iconWidth = this.$refs.iconContainer.clientWidth;

      switch (this.position) {
        case "left":
          this.left = -this.width;

          break;

        case "center":
          this.top = popoverHeight + 10;
          this.left = -(this.width / 2 - iconWidth / 2);

          break;

        default:
          this.left = iconWidth;
          this.top = popoverHeight;
      }
    }
  },
  methods: {
    onMouseEnter() {
      if (this.triggerType === "hover") {
        this.show = true;
      }
    },
    onMouseLeave() {
      if (this.triggerType === "hover") {
        this.show = false;
      }
    },
    onClick() {
      if (this.triggerType === "click") {
        this.show = !this.show;
      }

      this.$emit("click");
    },
    onClickOutside() {
      if (this.show) {
        this.show = false;
      }
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/variables.scss";

.v-info {
  user-select: none;
  position: relative;
}

.v-info__popover {
  display: none;
  position: absolute;
  padding: 10px 15px;
}

.v-info__popover--show {
  display: flex;
}

.v-info__icon-container {
  display: flex;
  cursor: pointer;
}

.v-info__popover-title {
  color: $white;
  user-select: auto;
}
</style>
