import { HttpClient } from "@/helpers/HttpClient";
import { sleep } from "@/utils/SystemUtils";
import { FormsMutations } from "@/store/modules/forms/mutations";

export const FormsActions = Object.freeze({
  GetFormsItemsAction: "Forms/GetFormsItemsAction",
  GetFormsListAction: "Forms/GetFormsListAction",
});

const GET_LIST_URL = "/mock/list.json";
const GET_ITEMS_URL = "/mock/items.json";

export const actions = {
  [FormsActions.GetFormsItemsAction]: async (context, meta) => {
    const httpClient = new HttpClient({});

    context.commit(FormsMutations.GetFormsItemsPerform, { meta });

    try {
      const data = await httpClient.get(GET_ITEMS_URL);

      // Имитация ожидания ответа от сервера
      await sleep();

      context.commit(FormsMutations.GetFormsItemsFulfill, {
        meta,
        payload: data[meta.id],
      });
    } catch (error) {
      context.commit(FormsMutations.GetFormsItemsReject, { error });
    }
  },
  [FormsActions.GetFormsListAction]: async (context, meta) => {
    const httpClient = new HttpClient({});

    context.commit(FormsMutations.GetFormsListPerform, { meta });

    try {
      const payload = await httpClient.get(GET_LIST_URL);

      // Имитация ожидания ответа от сервера
      await sleep();

      context.commit(FormsMutations.GetFormsListFulfill, {
        meta,
        payload,
      });
    } catch (error) {
      context.commit(FormsMutations.GetFormsListReject, { error });
    }
  },
};
